
/* notify block */
.bem-notify{
    background: #F2F2F2;
    background-blend-mode: multiply;
    border-radius: 15px;
    margin-top: 18px;
    width: 444px;
}


/* notify prloader */

.preloader{
    margin-top: 31px;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    color: #BDBDBD;
    display: flex;
    align-items: center;
    justify-content: center;
    max-width: 444px;
    cursor: pointer;
    &:after{
        content: "";
        display: block;
        background: url(/img/preloader.svg) no-repeat;
        background-size: contain;
        width: 12px;
        height: 7px; 
        margin-left: 7px;
    }
}

/* .leave{
    display: none;
} */


.active-item{
    font-weight: bold;
    color: #000000 !important;     
}


.hided{
    display: none;
    
}


