.border-bottom{
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
}

.bottom-remove{
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
}


.toggle-btn{
    background: url(/img/toggle_down.svg) no-repeat;
    background-size: contain;
    width: 21px;
    height: 12px;
    transform: rotate(0deg);
    transition: .3s;
}

.flex-wrapper .expand {
    background: #f2f2f2;
    border-bottom-left-radius: 0 ;
    border-bottom-right-radius: 0 ;
}

.expand .hided{
    display: block;
    transition: display 1s;
}



.expand .statistics__item-block{
    background: #ffecec;
}

.expand .toggle-btn{
    transform: rotate(180deg);
    transition: .3s;
}

/* passw */
.toggle-password{
    background-repeat:  no-repeat;
    background-size: cover;
    width: 31px;
    height: 31px;
    position: absolute;
    right: 0;
    top: 9px;
    right: 7px;
    cursor: pointer;

    /* top: 0;
    bottom: 0; */
}

.show-password{
    background-image: url(/img/hide.svg);
    
    &:hover{
        background-image: url(/img/hide_hover.svg);
   }
}
.hide-password{ 
    background-image: url(/img/eye.svg);
    
    &:hover{
        background-image: url(/img/eye_hover.svg);
   }
}

/* exit btn */
.exit-block{

    &__btn{
        
        cursor: pointer;
        background: url(/img/exit.svg) no-repeat;
        background-size: cover;
        width: 51px;
        height: 51px;
        margin-top: 40px;
        margin-right: 40px;
    }
}

/* overlay */
.expand .overlay{
    display: flex;
}

.overlay{
    display: none;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 3;
    background: hsla(0, 0%, 100%, 0.85);
    /* display: flex; */
    justify-content: center;
    align-items: center;
}

/* modal */

.bem-modal{
    box-shadow: 0px 3px 20px rgba(0, 0, 0, 0.06);
    padding:65px 62px 81px;
    max-width: 675px;
    background: #ffffff;
    border-radius: 15px;
    &__title{
        font-weight: 600;
        font-size: 36px;
        line-height: 43px;
        text-align: center;
        color: #000000;
    }


    &__params{
        margin-top: 93px;
        display: flex;
        justify-content: space-between;
        
        &-left, &-right{
            cursor: pointer;
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            text-align: center;
            &:hover{
                text-decoration: underline;
            }
        }

        
        &-left{
            color: #EB5757;
        }

        &-right{
            color: #BDBDBD;
        }
    }
}




@media screen and (max-width: 1300px){
    .bem-modal{
        padding: 45px 65px 53px
    }
    .bem-notify{
        width: 380px;
    }
}

@media screen and (max-width: 1024px){
    .bem-modal{
        

        &__title{
            font-size: 24px;
            line-height: 29px;
        }
        &__params{
            &-left{
                font-size: 18px;
                line-height: 21px;
            }
            &-right{
                font-size: 18px;
                line-height: 21px;
            }
        }
    }
    .bem-notify{
        width: 544px;
    }
}

@media screen and (max-width: 768px){
    .bem-modal{
        padding: 25px 35px 25px;

        &__title{
            font-size: 21px;
            line-height: 25px;
        }
        &__params{
            &-left{
                font-size: 16px;
                line-height: 19px;
            }
            &-right{
                font-size: 16px;
                line-height: 19px;
            }
        }
    }
    .bem-notify{
        width: 444px;
    }
}

@media screen and (max-width: 540px){
    .bem-modal{
        padding: 25px 35px 25px;
        margin-left: 10px;
        margin-right: 10px;
        &__title{
            font-size: 18px;
            line-height: 21px;
        }
        &__params{
            &-left{
                font-size: 16px;
                line-height: 19px;
            }
            &-right{
                font-size: 16px;
                line-height: 19px;
            }
        }
    }
    
    .exit-block{

        &__btn{
            
        
            width: 35px;
            height: 35px;
            margin-top: 15px;
        }
    }
    
    .bem-notify{
        width: 300px;
    }
}