/* @mixin reset-btn */

@define-mixin resetBtn  {
    border: none;
    outline: none;
    background: #fff0;
    /* font-size: 14px;
    @mixin font Montserrat, 14px; */
  }
 

@define-mixin font $name, $pix{
    font-family: $name;
    font-size: $pix;
} 


/* icon mixin */

@define-mixin icon $name{
  background-image: url(/img/$name.svg);
  background-size: contain;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 24px;
}