*{
 box-sizing: border-box;  
  
}        


@font-face {
    font-family: "SF UI Display";
    src: url("/fonts/SF UI Display Regular.ttf") format("ttf"),
         url("/fonts/sf-ui-display-black.otf") format("otf"),
         url("/fonts/sf-ui-display-bold.otf") format("otf"),
         url("/fonts/sf-ui-display-heavy.otf") format("otf"),
         url("/fonts/sf-ui-display-light.otf") format("otf"),
         url("/fonts/sf-ui-display-medium.otf") format("otf"),
         url("/fonts/sf-ui-display-semibold.otf") format("otf"),
         url("/fonts/sf-ui-display-ultralight.otf") format("otf");
     
}

@font-face {
    font-family: "SF Pro Icons";
    src: url("/fonts/sf-pro-icons_regular.woff2") format("woff2");
     
}
html, body{
    height: 100%;
}

body{
    padding: 0px;
    margin: 0;
    font-family: 'SF UI Display';
    font-size: 14px;
    min-height: 100%;
    /* height: 100vh; */
    scroll-behavior: smooth;
    text-rendering: optimizeSpeed;
    
}

a{
    text-decoration: none;
}

.site-carcass{
    height: 100%;
    &__container{
        display: flex;
        flex-direction: column;
        height: 100%;
    }
}


/* bg user */
.bg-abs{
    background: rgba(255, 255, 255, 0.5);
    backdrop-filter: blur(40px);
}

.bg-user{
    background: url(/img/bg-user.jpg) no-repeat;
    background-size: cover;
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
}


