.auth{
    height: 100%;
    padding-left: 129px;
    background: #ffffff;
    position: relative;
    overflow: hidden;
    background-image: url(/img/bg.jpg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: right bottom;
    &__header{         
        margin-top: 127px;

        a{
            display: block;
            width: 88px;
            height: 88px;
            img{
                width: 100%;
                height: 100%;
            }
        }
    }
/* bg auth */

    &-body{
        z-index: 1;
        &__title{
            font-weight: 500;
            font-size: 64px;
            line-height: 76px;
            color: #000000;
            margin-top: 77px;
        }
    }

    &-form{
        margin-top: 152px;
        max-width: 368px;
        width: 100%;


        &__login, &__password{
            position: relative;
            input{
                background: #FFFFFF;
                border: 1px solid #E0E0E0;
                box-sizing: border-box;
                border-radius: 7px;
                font-weight: normal;
                font-size: 18px;
                line-height: 21px;
                color: rgb(97, 97, 97);
                width: 100%;
                padding: 13px 0 15px 19px;                
                outline: none;

                &::placeholder{
                    font-weight: normal;
                    font-size: 18px;
                    line-height: 21px;

                    color: #BDBDBD;

                }
            }
        }
        &__password{
            margin-top: 25px;
        }

        &__btn{
            margin-top: 25px;
            button{
                @mixin resetBtn;
                width: 100%;
                background: #2F80ED;
                border-radius: 7px;
                font-weight: bold;
                font-size: 18px;
                line-height: 21px;
                text-align: center;
                color: #FFFFFF;
                text-align: center;
                padding: 13px 0 15px 0;
                font-family: "SF UI Display";
                cursor: pointer;
            }
        }

        &__forget{
            margin-top: 26px;
            font-weight: normal;
            font-size: 13px;
            line-height: 16px;
            color: #333333;
            a{
                text-decoration-line: underline;
                color: #2F80ED;
            }
        }
    }
}



@media screen and (max-width: 1600px){
    .auth{
        &-body{
            &__title{
                color: #2f7fed;
            }
        }
    }
} 

@media screen and (max-width: 1024px){
    .auth{
        background-image: none;
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-left: 0;
        &-body{
            &__title{
                text-align: center;
                color: #000;
            }
        }
        &-form{
            max-width: unset;
        }
    }
} 

@media screen and (max-width: 768px){
    .auth{
       padding-left: 13px;
       padding-right: 13px;
        &__header{
            margin-top: 65px;
        }
        &-body{
            &__title{
                font-size: 28px;
                line-height: 33px;
            }
        }

        &-form{
            margin-top: 30px;
        }
        &-form{
           
    
    
            &__login, &__password{
               
                input{
                    padding: 7px 0 9px 13px;
                    font-size: 14px;
                    line-height: 17px;
    
                    &::placeholder{
                        font-size: 14px;
                        line-height: 17px;
    
                    }
                }
            }
            &__password{
                .toggle-password{
                    width: 24px;
                    height: 24px;
                    top: 6px;
                }
            }
    
            &__btn{
               
                button{
                    font-size: 14px;
                    line-height: 17px;
                }
            }
    
            &__forget{
                
                a{
                    
                }
            }
        }
       
    }
} 