.admin-panel{
    padding-left: 128px;
    margin-right: 182px;

    &__title{
        margin-top: 37px;
        font-weight: 500;
        font-size: 64px;
        line-height: 76px;
        max-width: 444px;
        /* &:after{
            content: "";
            display: block;
            position: absolute;
            height: 831px;
            width: 1px;
            background: #E0E0E0;
            right: -182px;
            top: 0;
        } */
        color: #000000;
        span{
            font-weight: 300;
        }
    }

    &__menu{
        margin-top: 86px;

        &-item{
            cursor: pointer;
            font-weight: normal;
            font-size: 36px;
            line-height: 43px;
            color: #000000;
            &:not(:first-child){
                margin-top: 24px;
            }
        }
    }

    &__message{
        margin-top: 190px;

        &-title{
            font-weight: 600;
            font-size: 24px;
            line-height: 29px;
            color: #333333;
        }

        &-input{
            margin-top: 23px;
            height: 167px;
            position: relative;
            textarea{
                outline: none;
                border: none;
                background: none;
                width: 100%;
                height: 100%;
                padding: 25px 25px 0 27px;
                resize: none;
                font-family: "SF UI Display";
                font-weight: normal;
                font-size: 18px;
                line-height: 21px;
                color: #6f6f6f;
                &::placeholder{
                    color: #BDBDBD;
                }
            }

            .send{
                font-weight: 600;
                font-size: 18px;
                line-height: 22px;
                color: #2F80ED;
                position: absolute;
                right: 32px;
                bottom: 19px;
                cursor: pointer;
            }
        }
    }
}


.features{
    padding-left: 108px;
    position: relative;

    &:after{
        content: "";
        display: block;
        position: absolute;
        height: 765px;
        width: 1px;
        background: #E0E0E0;
        left: 0px;
        top: 128px;
    }
    &__title{
        margin-top: 128px;
        font-weight: normal;
        font-size: 36px;
        line-height: 43px;
        color: #000000;
       
       
    }
    .margin{
        margin-top: 80px;
    }
    .statistics{


        &__header{
            display: flex;
            
            &-item{
                font-weight: bold;
                font-size: 24px;
                line-height: 29px;
                /* identical to box height */
                color: #BDBDBD;
                cursor: pointer;
      
                &:not(:last-child){
                    margin-right: 17px;
                }
              

                
            }
        }

        &__grid{
            margin-top: 36px;
            display: none;
            flex-wrap: wrap;
            

            &-block{
                display: flex;
            }

            .flex-wrapper{
                display: inline-block;
            }
        }

        &__item{
            /* background: #F2F2F2;
            
            background-blend-mode: multiply; */
            border-radius: 15px;
            background: #f2f2f2;
            width: 444px;
            margin-right: 20px;
            margin-bottom: 20px;
            &-block{
                background: #F2F2F2;
                background-blend-mode: multiply;
                border-radius: 15px;
                padding: 36px 32px  35px 45px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                cursor: pointer;
                user-select: none;
            }

            &-title{
                font-weight: 500;
                font-size: 24px;
                line-height: 29px;
                color: #000000;
           
            }
            position: relative;
            .hided{ 
                width: 100%;
                z-index: 2;
                position: absolute;
                background: #F2F2F2;
                border-bottom-left-radius: 15px;
                border-bottom-right-radius: 15px;
            }

        }

        &__item-event{
            padding-left: 45px;
            padding-right: 39px;
            padding-bottom: 38px;
            &:first-child{
                border-bottom: 1px solid #BDBDBD;
            }
            &__title{
                font-weight: normal;
                font-size: 24px;
                line-height: 29px;
                color: #000000;
                margin-top: 35px;
         
            }

            &__info{
                margin-top: 10px;
                display: flex;
                align-items: center;
                justify-content: space-between;

                .time{
                    font-weight: 500;
                    font-size: 34px;
                    line-height: 57px;
                    color: #000000;
                }

                .stat{
                    max-width: 190px;
                    font-weight: normal;
                    font-size: 18px;
                    line-height: 18px;
                    color: #000000;
                    
                    &:before{
                        content: "";
                        display: inline-block;
                        background: url(/img/down.svg) no-repeat;
                        background-size: contain;
                        width: 19px;
                        height: 19px;
                        margin-right: 3px;
                        vertical-align: middle;
                    }
                } 
            }
        }




    }
}



