 .content{
     height: 100%;
     display: flex;
     /* justify-content: space-between; */
     overflow-x: hidden;
 }

 .user-panel{
     padding-left: 129px;

     .weather-msg{
         margin-top: 52px;
         font-weight: normal;
        font-size: 24px; 
        line-height: 29px;
        color: #000000;
     }

     &__title{
        margin-top: 30px;
        font-weight: 500;
        font-size: 64px; 
        line-height: 76px;  
        color: #000000;
        width: 705px;
        span{
            font-weight: 300;
        }
     }

     &__info{
        margin-top: 67px;

         &-title{
            font-weight: 500;
            font-size: 24px;
            line-height: 29px;
            color: #000000;
         }

         &-block{
            margin-top: 20px;
            display: flex; 
            align-items: center;
            padding: 22px 39px 21px 45px;
           
             .time{
                font-weight: 500;
                font-size: 48px;
                line-height: 57px;
                text-align: center;                
                color: #000000;
                margin-right: 38px;
             }

             .info{
                font-weight: normal; 
                font-size: 18px;
                line-height: 18px;
                color: #000000;

                
             }
             .stat-down{
                &:before{ 
                    content: "";
                    display: inline-block;
                    background: url(/img/down.svg) no-repeat;
                    background-size: contain;
                    width: 24px;
                    height: 24px;
                    margin-right: 5px;
                    vertical-align: middle;
                }
             }
         }    
     }
     &__notify{
        margin-top: 67px;
       &-title{
           font-weight: 500;
           font-size: 24px;
           line-height: 29px;                
           color: #000000;
           margin-bottom: 2px; 
       }
        .notify-block{
           padding: 21px 0 27px 35px;

            &__title{
               font-weight: bold;
               font-size: 18px;
               line-height: 21px;
               
               color: #333333;
               
            }

            &__msg{
                margin-top: 18px;
                max-width: 382px;
                font-weight: normal;
               font-size: 18px;
               line-height: 21px;
               color: #333333;
            }
        }
    }
 }

 .timer{
    width: 100%;
    display: flex;
    /* flex-direction: column; */
    justify-content: flex-end;
    padding-right: 143px;
    .progress {
        position: absolute;
        transform: rotate(-90deg);
      }
      .progress__meter, .progress__value {
          fill: none;
          transform: translate(50%, 50%) ;
      }
      .progress__meter {
          stroke: #e6e6e6;
      }
      .progress__value {
          stroke: #f2f2f2;
          stroke-linecap: round;
      }

      
     &__circle{
         width: 764px;
         height:  764px;
         margin-top: 153px;
         border-radius: 150%;
         /* border: 6px solid #F2F2F2; */
         display: flex;
         flex-direction: column;
         justify-content: center;
         align-items: center;
     }

     &__date{
        font-weight: normal;
        font-size: 36px;
        line-height: 43px;       
        text-align: center;        
        color: #000000;
     }

     &__clock{
        margin-top: 15px;
        font-weight: 500;
        font-size: 180px;
        line-height: 215px;    
        text-align: center;        
        color: #000000;      
     }

     .green{
        border: 1px solid #27AE60;
        color: #27AE60;
        &:hover{
            background: #27AE60; 
        }
        &:after{
            background-image: url(/img/repeat_green.svg);
        }
     }

     .yellow{
        border: 1px solid #F2C94C;
        color: #F2C94C;
        &:hover{
            background: #F2C94C; 
        }
        &:after{
            background-image: url(/img/repeat_yellow.svg);
        }
     }

     .red{
        border: 1px solid #FF0000;
        color: #FF0000;
        &:hover{
            background: #FF0000; 
        }
        &:after{
            background-image: url(/img/repeat_red.svg);
        }
     }

     &__btn{
        @mixin resetBtn; 
        margin-top: 61px;
        box-sizing: border-box;
        border-radius: 15px;
        width: 305px;
        font-weight: bold;
        font-size: 24px;
        line-height: 29px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 25px 32px 24px;
        cursor: pointer;
        user-select: none;
        z-index: 2;
        &:hover{
            color: #ffffff;
            &:after{
                background-image: url(/img/repeat_white.svg);
            }
        }
        &:after{
            content: "";
            display: block; 
            background: url(/img/repeat.svg) no-repeat;
            background-size: contain;
            width: 20px;
            height: 19px;
            margin-left: 5px;
        }
     }

  
 }




 @media screen and (max-width: 1620px){
    .content{
        
        .user-panel{
            &__title{
                width: unset;
            }
        }

        .timer{
            &__circle{
    
                svg{
                    width: 584px;
                    height:  584px;
                }

              
            }
            &__clock{
                font-size: 120px;
                line-height: 143px;
            }
            &__btn{
                font-size: 18px;
                line-height: 21px;
                padding: 15px 25px 17px ;
                width: 240px;
            }
        }
    }

 }

